* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body,
html {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  font-size: 12px;
  color: #424242;
  scroll-behavior: smooth;
}

/* Header Styles */
.header {
  width: 100%;
  min-height: 40px;
}

.header__inner {
  width: 100%;
  text-align: center;
}

.header__title {
  font-weight: 200;
  font-size: 2.5rem;
  margin: 0.5em 0.25em;
  display: inline-block;
  color: #212121;
}

.header__menu {
  font-size: 32px;
  margin: 10px auto;
  display: block;
  width: 44px;
  padding: 0.5rem;
}

/* Navigation Styles */
.nav__wrap {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  background-color: #1f1d1d;
}

.nav__list {
  width: 100%;
  padding: 0;
  margin: 0;
  text-align: center;
}

.nav__item {
  display: inline-block;
  min-width: 20%;
  text-align: center;
  line-height: 24px;
  text-transform: uppercase;
  font-weight: 400;
  text-decoration: none;
  color: #ddd;
}

.nav__item a {
  display: inline-block;
  padding: 1em;
  text-decoration: none;
  color: #ddd;
  transition: 0.2s ease;
}

.nav__item a:hover {
  background-color: #ddd;
  color: #000;
}

