.skip__content {
  position: absolute;
  top: -100px;
  left: 0;
  background-color: #bf1722;
  color: white;
  padding: 10px;
  font-size: 20px;
  z-index: 100;
  text-decoration: none;
  border: none;
}

.skip__content:focus {
  top: 150px;
}

/* Jumbotron Styles */
.hero {
  display: flex;
  min-height: 40em;
  max-width: 100%;
  object-fit: cover;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(rgba(0, 0, 5, 0.2), rgba(0, 0, 5, 0.5)),
    url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

