;AAEA;EACE,aAAa;EACb,SAAS;AACX;;AAEA;;EAEE,OAAO;AACT;;AAEA;;EAEE,gBAAgB;EAChB,UAAU;AACZ;;AAEA;;EAEE,mBAAmB;EACnB,aAAa;EACb,mBAAmB;AACrB;;AAEA;;EAEE,kBAAkB;EAClB,gBAAgB;AAClB;;AAEA,SAAS;;AAET;EACE,eAAe;EACf,kBAAkB;EAClB,kBAAkB;EAClB,aAAa;EACb,mBAAmB;EACnB,YAAY;EACZ,eAAe;EACf,eAAe;EACf,aAAa;EACb,WAAW;AACb;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,WAAW;AACb","sourcesContent":["/* Reset Styles */\r\n* {\r\n  padding: 0;\r\n  margin: 0;\r\n  box-sizing: border-box;\r\n}\r\n\r\nbody,\r\nhtml {\r\n  width: 100%;\r\n  height: 100%;\r\n  margin: 0;\r\n  padding: 0;\r\n  font-family: \"Poppins\", sans-serif;\r\n  font-size: 12px;\r\n  color: #424242;\r\n  scroll-behavior: smooth;\r\n}\r\n\r\n/* Header Styles */\r\n.header {\r\n  width: 100%;\r\n  min-height: 40px;\r\n}\r\n\r\n.header__inner {\r\n  width: 100%;\r\n  text-align: center;\r\n}\r\n\r\n.header__title {\r\n  font-weight: 200;\r\n  font-size: 2.5rem;\r\n  margin: 0.5em 0.25em;\r\n  display: inline-block;\r\n  color: #212121;\r\n}\r\n\r\n.header__menu {\r\n  font-size: 32px;\r\n  margin: 10px auto;\r\n  display: block;\r\n  width: 44px;\r\n  padding: 0.5rem;\r\n}\r\n\r\n/* Navigation Styles */\r\n.nav__wrap {\r\n  width: 100%;\r\n  margin-left: auto;\r\n  margin-right: auto;\r\n  text-align: center;\r\n  background-color: #1f1d1d;\r\n}\r\n\r\n.nav__list {\r\n  width: 100%;\r\n  padding: 0;\r\n  margin: 0;\r\n  text-align: center;\r\n}\r\n\r\n.nav__item {\r\n  display: inline-block;\r\n  min-width: 20%;\r\n  text-align: center;\r\n  line-height: 24px;\r\n  text-transform: uppercase;\r\n  font-weight: 400;\r\n  text-decoration: none;\r\n  color: #ddd;\r\n}\r\n\r\n.nav__item a {\r\n  display: inline-block;\r\n  padding: 1em;\r\n  text-decoration: none;\r\n  color: #ddd;\r\n  transition: 0.2s ease;\r\n}\r\n\r\n.nav__item a:hover {\r\n  background-color: #ddd;\r\n  color: #000;\r\n}\r\n\r\n/* Skip Content Styles */\r\n/* .skip__content {\r\n  position: absolute;\r\n  top: 0px;\r\n  left: 0;\r\n  background-color: #bf1722;\r\n  color: white;\r\n  padding: 10px;\r\n  font-size: 20px;\r\n  z-index: 100;\r\n  text-decoration: none;\r\n  border: none;\r\n} */\r\n.skip__content {\r\n  position: absolute;\r\n  top: -100px;\r\n  left: 0;\r\n  background-color: #bf1722;\r\n  color: white;\r\n  padding: 10px;\r\n  font-size: 20px;\r\n  z-index: 100;\r\n  text-decoration: none;\r\n  border: none;\r\n}\r\n\r\n.skip__content:focus {\r\n  top: 150px;\r\n}\r\n\r\n/* Jumbotron Styles */\r\n.hero {\r\n  display: flex;\r\n  min-height: 40em;\r\n  max-width: 100%;\r\n  object-fit: cover;\r\n  background-size: cover;\r\n  background-repeat: no-repeat;\r\n  background-position: center;\r\n  justify-content: center;\r\n  align-items: center;\r\n  background-image: linear-gradient(rgba(0, 0, 5, 0.2), rgba(0, 0, 5, 0.5)),\r\n    url(\"../public/sharpHeros/hero-image_4-large.jpg\");\r\n}\r\n\r\n@media (max-width: 760px) {\r\n  .hero {\r\n    /* background-image: url('../public/sharpHeros/hero-image_4-small.jpg'); */\r\n    background-image: image-set('../public/sharpHeros/hero-image_4-small.jpg') linear-gradient(rgba(0, 0, 5, 0.2), rgba(0, 0, 5, 0.5));\r\n  }\r\n}\r\n\r\n@media (min-width: 900px) {\r\n  .hero {\r\n    /* background-image: url('../public/sharpHeros/hero-image_4-large.jpg'); */\r\n    background-image: image-set('../public/sharpHeros/hero-image_4-large.jpg') linear-gradient(rgba(0, 0, 5, 0.2), rgba(0, 0, 5, 0.5));\r\n  }\r\n}\r\n\r\n/* @media screen and (min-widht: 480px) {\r\n  .hero{\r\n    background-image: url('../../dist//heros/hero-image_4-small.jpg');\r\n  }\r\n  body{\r\n    background-color: greenyellow;\r\n  }\r\n}\r\n@media screen and (min-widht: 1400px) {\r\n  .hero{\r\n    background-image: url('../../dist//heros/hero-image_4-large.jpg');\r\n  }\r\n  body{\r\n    background-color: blueviolet;\r\n  }\r\n} */\r\n\r\n/* .hero {\r\n  display: flex;\r\n  min-height: 40em;\r\n  max-width: 100%;\r\n  object-fit: cover;\r\n  background-position: center;\r\n  background-repeat: no-repeat;\r\n  justify-content: center;\r\n  align-items: center;\r\n  background-image: linear-gradient(rgba(0, 0, 5, 0.2), rgba(0, 0, 5, 0.5)),\r\n    url(\"../public/heros/hero-image_4.jpg\");\r\n  background-image: \r\n    -webkit-image-set(\r\n      url('../public/sharpHeros/hero-image_4-small.jpg') 1x,\r\n      url('../public/sharpHeros/hero-image_4-large.jpg') 2x\r\n    );\r\n  background-image:\r\n    image-set(\r\n      url('../public/sharpHeros/hero-image_4-small.jpg') 1x,\r\n      url('../public/sharpHeros/hero-image_4-large.jpg') 2x\r\n    );\r\n} */\r\n\r\n.hero__inner {\r\n  text-align: center;\r\n  color: #dddd;\r\n  margin: 0 auto;\r\n  max-width: 800px;\r\n}\r\n\r\n.hero__inner h1 {\r\n  color: #fff;\r\n  font-weight: 600;\r\n  font-size: 36px;\r\n  text-shadow: 1px 1px #141414;\r\n}\r\n\r\n.hero__inner p {\r\n  margin-top: 0.5em;\r\n}\r\n\r\n/* Main Content Styles */\r\nmain {\r\n  width: 100%;\r\n  margin: 0 auto;\r\n}\r\n\r\n.content__label {\r\n  font-size: 32px;\r\n  font-weight: bold;\r\n  margin-top: 0.5em;\r\n  text-align: center;\r\n}\r\n\r\n.content {\r\n  margin: 32px;\r\n  text-align: left;\r\n}\r\n\r\n.post-item {\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n  width: 100%;\r\n  border-radius: 5px;\r\n  overflow: hidden;\r\n}\r\n\r\n.post-city {\r\n  position: absolute;\r\n  border-radius: 5px;\r\n  background-color: #135ea8a2;\r\n  min-width: 6em;\r\n  padding: 0.5em;\r\n  color: white;\r\n  font-weight: 500;\r\n  margin: 14px 0;\r\n  text-align: center;\r\n}\r\n\r\n.post-pics {\r\n  width: 100%;\r\n  height: 50%;\r\n}\r\n\r\n.post-item-content {\r\n  padding: 1.5em;\r\n  width: 100%;\r\n  margin: 0;\r\n}\r\n\r\n.post-item-rating {\r\n  font-size: 12px;\r\n  text-transform: uppercase;\r\n  color: #999;\r\n  font-weight: 500;\r\n}\r\n\r\n.list-rating-value {\r\n  color: red;\r\n  text-decoration: none;\r\n  font-size: 2em;\r\n  padding: 10px;\r\n  min-width: 44px;\r\n  display: inline-block;\r\n}\r\n\r\n.post-name {\r\n  font-weight: 500;\r\n  font-size: 16px;\r\n  margin-top: 10px;\r\n  transition: 0.3s;\r\n  width: 100%;\r\n  align-items: center;\r\n}\r\n\r\n.post-name:hover {\r\n  opacity: 0.5;\r\n}\r\n\r\n.post-name a {\r\n  text-decoration: none;\r\n  color: black;\r\n  font-size: 1.5rem;\r\n  padding: 10px;\r\n}\r\n\r\n.post-desc {\r\n  margin-top: 10px;\r\n  font-size: 12px;\r\n  line-height: 1.5em;\r\n}\r\n\r\n/* Footer Styles */\r\nfooter {\r\n  background-color: #135ea8;\r\n  color: white;\r\n  font-size: 14px;\r\n  padding: 2.5em;\r\n  width: 100%;\r\n  text-align: center;\r\n}\r\n\r\n/* Restaurant Card Styles */\r\n.restaurant-card {\r\n  display: block;\r\n  width: 250px;\r\n  margin: 10px;\r\n  border: 1px solid #ddd;\r\n  border-radius: 8px;\r\n  overflow: hidden;\r\n  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);\r\n  transition: transform 0.2s ease-in-out;\r\n  cursor: pointer;\r\n  text-decoration: none;\r\n}\r\n\r\n.restaurant-card:hover {\r\n  transform: scale(1.05);\r\n}\r\n\r\n.restaurant-image {\r\n  width: 100%;\r\n  height: 150px;\r\n  object-fit: cover;\r\n  border-radius: 8px 8px 0 0;\r\n}\r\n\r\n.restaurant-info {\r\n  padding: 16px;\r\n}\r\n\r\n.restaurant-title {\r\n  font-size: 1.2em;\r\n  margin-bottom: 8px;\r\n  color: #333;\r\n}\r\n\r\n.restaurant-description {\r\n  color: #555;\r\n}\r\n\r\n.restaurant-city {\r\n  font-style: italic;\r\n  color: #888;\r\n  margin-bottom: 8px;\r\n}\r\n\r\n/* Restaurant List Styles */\r\n.list-resto {\r\n  display: flex;\r\n  flex-wrap: wrap;\r\n  justify-content: start;\r\n  align-items: start;\r\n  padding: 20px;\r\n  gap: 20px;\r\n}\r\n\r\n/* Main Restaurant Container Styles */\r\n.main-resto_container {\r\n  padding: 20px;\r\n}\r\n\r\n.main-resto_label {\r\n  font-size: 1.5em;\r\n  margin-bottom: 20px;\r\n}\r\n\r\n/* Form Container Styles */\r\n.form-review {\r\n  margin: 20px;\r\n}\r\n\r\n/* Form Styles */\r\n.review-form {\r\n  max-width: 400px;\r\n  margin: auto;\r\n}\r\n\r\n.form-label {\r\n  font-size: 1.2em;\r\n  margin-bottom: 5px;\r\n  display: block;\r\n}\r\n\r\n.form-control {\r\n  width: 100%;\r\n  padding: 8px;\r\n  margin-bottom: 15px;\r\n  box-sizing: border-box;\r\n  border: 1px solid #ccc;\r\n  border-radius: 4px;\r\n  font-size: 1em;\r\n}\r\n\r\ntextarea.form-control {\r\n  resize: vertical;\r\n}\r\n\r\n/* Submit Button Styles */\r\n.btn {\r\n  background-color: #4caf50;\r\n  color: white;\r\n  padding: 10px 15px;\r\n  border: none;\r\n  border-radius: 4px;\r\n  cursor: pointer;\r\n}\r\n\r\n.btn:hover {\r\n  background-color: #45a049;\r\n}\r\n\r\n/* Optional: Add a primary button style */\r\n.btn-primary {\r\n  background-color: #007bff;\r\n}\r\n\r\n.btn-primary:hover {\r\n  background-color: #0056b3;\r\n}\r\n\r\n/* Detail Container Styles */\r\n.detail {\r\n  margin: 20px;\r\n  padding: 20px;\r\n  border: 1px solid #ddd;\r\n  border-radius: 8px;\r\n  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);\r\n  overflow: hidden;\r\n}\r\n\r\n/* Detail Info Container Styles */\r\n.container-info {\r\n  display: flex;\r\n}\r\n\r\n/* Detail Image Container Styles */\r\n.img-container {\r\n  margin-right: 20px;\r\n}\r\n\r\n/* Detail Info Styles */\r\n.detail-info {\r\n  list-style: none;\r\n  padding: 0;\r\n}\r\n\r\n.detail-info li {\r\n  margin-bottom: 10px;\r\n}\r\n\r\n/* Resto Name, Address, and Rating Styles */\r\n.resto-name,\r\n.resto-address,\r\n.resto-rating {\r\n  display: flex;\r\n  align-items: center;\r\n}\r\n\r\n.resto-name i,\r\n.resto-address i,\r\n.resto-rating i {\r\n  margin-right: 10px;\r\n}\r\n\r\n/* Menu List Styles */\r\n.restaurant-detail__menu-list {\r\n  display: flex;\r\n}\r\n\r\n/* Foods and Drinks Styles */\r\n.foods,\r\n.drinks {\r\n  margin-right: 20px;\r\n}\r\n\r\n.restaurant-detail__foods,\r\n.restaurant-detail__drinks {\r\n  list-style: none;\r\n  padding: 0;\r\n  font-size: 20px;\r\n  color: gray;\r\n}\r\n\r\n.restaurant-detail__foods li,\r\n.restaurant-detail__drinks li {\r\n  margin-bottom: 10px;\r\n}\r\n\r\n/* Review Styles */\r\n.detail-review-item